.firstpage {
  display: flex;
  flex-direction: row;
  flex-flow: row wrap;
  justify-content: space-evenly;
  align-content: center;
  background-color: rgb(243, 243, 243);
  flex-wrap: wrap;
  height: 100vh;
}

.info .name {
  font-family: "Times New Roman", Times, serif;
  font-size: 4vw;
  color: rgb(128, 108, 93);
  font-weight: 300;
  text-align: center;
  margin: 0;
  letter-spacing: 6px;
}
.info .role {
  font-family: "Times New Roman", Times, serif;
  font-size: 4vw;
  color: rgb(197, 197, 197);
  font-weight: 100;
  text-align: center;
  margin: 0 0 0 0px;
  letter-spacing: 8px;
}

.arrow-down {
  border-right: 4px solid rgb(100, 100, 100);
  border-top: 4px solid rgb(100, 100, 100);
  height: 1rem;
  position: absolute;
  bottom: 5vh;
  transform: rotate(135deg);
  width: 1rem;
}
@media (max-width: 650px) {
  .arrow-down {
    border-right: 1px solid rgb(100, 100, 100);
    border-top: 1px solid rgb(100, 100, 100);
  }
}
