.contact {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-content: center;
  font-size: large;
  height: 70vh;
  flex-wrap: wrap;
}
.content {
  justify-content: Left;
  font-size: normal;
  text-align: center;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-weight: lighter;
  line-height: 1.8;
  padding-top: 4%;
  padding-right: 20%;
  padding-left: 20%;
}
.content a {
  color: #9c0039;
  text-decoration: none;
}
.contact h2 {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  color: rgb(121, 121, 121);
  font-weight: normal;
  font-size: x-large;
  letter-spacing: 1vh;
  text-transform: uppercase;
}
