.about {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-content: center;
  font-size: large;
  height: auto;
  flex-wrap: wrap;
  text-align: left;
  padding-top: 4%;
  padding-left: 22%;
  padding-right: 22%;
}

.aboutContent {
  justify-content: center;
  font-size: normal;
  text-align: center;
}

.about h2 {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  color: rgb(121, 121, 121);
  font-weight: normal;
  font-size: x-large;
  letter-spacing: 1vh;
  text-transform: uppercase;
}

.aboutContent p {
  font-size: normal;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  color: rgb(42, 42, 42);
  line-height: 1.8;
  font-weight: lighter;
  text-align: left;
}
.meImage {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 5%;
}

.me {
  width: 50vh;
  filter: grayscale(100%);
}
