.projectMain {
  background-color: rgb(243, 243, 243);
  text-align: left;
  padding-right: 22%;
  padding-left: 22%;
  height: auto;
  padding-top: 4%;
}

.flexProjects {
  display: flex;
  flex-flow: column;
  justify-content: space-between;
  height: auto;
}

h3 {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  color: rgb(121, 121, 121);
  font-weight: normal;
  font-size: normal;
  letter-spacing: 1vh;
}

p {
  font-size: normal;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  color: rgb(42, 42, 42);
  line-height: 1.8;
  font-weight: lighter;
  text-align: left;
}
.projectContent h2 {
  font-family: sans-serif;
  font-weight: normal;
  font-size: x-large;
}

.projectMain h2 {
  /* font-family: sans-serif; */
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  color: rgb(121, 121, 121);
  font-weight: normal;
  font-size: x-large;
  letter-spacing: 1vh;
  text-transform: uppercase;
  padding-bottom: 4%;
}
.Imageholder {
  width: 50%;
}

.example {
  display: flex;
  flex-flow: row;
  height: auto;
  width: 100%;
  padding-bottom: 10%;
  text-align: left;
  position: relative;
  font-size: 30px;
}

.projectImage {
  width: 100%;
  height: auto;
}

.projectContent {
  align-items: left;
  justify-content: left;
  /*object-fit: cover;*/
  font-weight: normal;
  font-size: small;
  letter-spacing: 1vh;
  margin-left: 4%;
  width: 50%;
}

@media (max-width: 650px) {
  .flexProjects {
    display: flex;
    flex-flow: column;
    justify-content: space-between;
    height: auto;
    align-items: center;
  }

  .example {
    display: flex;
    flex-flow: column;
    height: auto;
    width: 100%;
    padding-bottom: 20%;
    text-align: left;
    position: relative;
    font-size: small;
  }
  .projectMain h2 {
    text-align: left;
    margin-bottom: 50px;
  }

  .projectContent {
    margin: 0;
    width: 100%;
  }

  .Imageholder {
    width: 100%;
  }
}
