.footer {
  display: flex;
  flex-direction: column;
  align-content: center;
  background-color: rgb(122, 108, 93);
  height: 30vh;
  text-justify: center;
  color: lightgray;
  justify-content: space-evenly;
}
.footer a {
  color: #d2d2d2;
}

.footer p {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  letter-spacing: 2px;
  font-weight: 250;
  color: #d2d2d2;
}

.footert {
  display: flex;
  flex-direction: row;
  text-align: center;
  justify-content: center;
  align-items: center;
  justify-content: space-around;
  padding-left: 35%;
  padding-right: 35%;
}

.footersocial {
  display: flex;
  flex-direction: row;
  text-align: center;
  justify-content: center;
  align-items: center;
  justify-content: space-around;
  padding-left: 30%;
  padding-right: 30%;
}

.footersocial a {
  color: #d2d2d2;
}
