@import url("https://fonts.googleapis.com/css?family=Source+Sans+Pro:regular,bold,italic&subset=latin,latin-ext");

.navigation {
  background-color: rgb(243, 243, 243);
  width: 100%;
  height: 80px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  display: flex;
  scroll-behavior: smooth;
  transition: ease-in-out;
  align-items: right;
  position: fixed;
  z-index: 1;
}

.navigation .rightside {
  flex: 50%;
  display: flex;
  justify-content: left;
  align-items: center;
  text-align: right;
}
.navigation .leftside {
  flex: 40%;
  display: flex;
  justify-content: right;
  align-items: center;
  text-align: left;
}

.navigation .leftside .links {
  max-height: 80px;
  float: right;
}

.navigation .leftside .links a {
  text-decoration: none;
  color: black;
  font-size: 1, 5rem;
  margin-left: 15px;
  text-align: right;
  line-height: 100px;
  padding: 0 20px;
  letter-spacing: 4px;
  color: rgb(121, 121, 121);
  text-decoration: none;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

.nolink {
  text-decoration: none;
}
h4 {
  margin: 2px;
}

.name {
  text-decoration: none;
}

.leftside .links a:hover {
  color: #c6afb1;
}

.navigation .leftside button {
  display: none;
  position: center;
}

.navigation .leftside #hidden {
  display: flex;
}

@media (max-width: 650px) {
  .navigation .leftside a {
    font-size: 10px;
    transition: ease-in-out;
  }

  .navigation .leftside button {
    position: center;
    display: flex;
    margin-left: 20px;
    color: rgb(94, 94, 94);
    border-style: hidden;
    background-color: transparent;
  }
  .navigation .leftside .links {
    display: none;
  }

  .navigation .rightside {
    display: none;
  }
  .navigation #hidden {
    background-color: white;
  }
  .navigation .leftside #hidden {
    position: absolute;
    left: 0px;
    top: 80px;
    height: 230px;
    max-height: 230px;
    width: 100%;
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .navigation .leftside #hidden a {
    margin: 20px;
    font-size: 15px;
    padding: 0 5px;
    line-height: 0px;
    padding: 4%;
  }
}
